body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-form {
  position: fixed;
  text-align: center;
  height: 70px;
  width: 100%;
  background-color: blue;
}

input {
  margin-top: 15px;
  height: 40px;
  width: 35%;
  border-radius: 10px;
  outline: none;
}

.submit-btn {
  padding: 8px 20px;
  border-radius: 10px;
  cursor: pointer;
  &:hover,
  &:focus {
    transform: scale(1.05);
    background-color: #fff;
  }
}

.gallery {
  display: flex;
  flex-direction: inherit;
  flex-wrap: wrap;
  padding-top: 70px;
  justify-content: center;
}

.link-wrap {
  text-decoration: none;
  margin-top: 20px;
  margin-left: 20px;
  border: 2px solid black;
  border-radius: 15px;
  overflow: hidden;
  flex-basis: calc((100% - 80px) / 4);
  transition: all 0.3s;
  &:hover,
  &:focus {
    transform: scale(1.05);
  }
}

.gallery-image {
  object-fit: cover;
  height: 300px;
}

.info {
  display: flex;
  text-align: center;
  justify-content: space-around;
}

.info-item {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 14px;
  color: black;
}

.item-number {
  font-weight: 400;
}

.load-more.is-hidden {
  display: none;
}
.load-more {
  display: block;
  margin: 20px auto;
  padding: 8px 20px;
  border-radius: 10px;
  cursor: pointer;
  &:hover,
  &:focus {
    transform: scale(1.05);
    background-color: blue;
  }
}
